import { time } from '@devsnc/sn-controls-common';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { MonthlyGrid } from '@servicenow/now-record-mini-calendar';

import { MONTHLY_FORMAT } from '../constants';
import pickerConfirm from '../picker-confirm';

export default (
	{
		highlightedMonth,
		hoveredMonth,
		properties: {
			language,
			start,
			end,
			visibleYear = start || time(language)(),
			requireConfirmation
		}
	},
	{ dispatch, updateState }
) => {
	// todo: move into state?
	visibleYear = time(language)(visibleYear, MONTHLY_FORMAT);
	start = time(language)(start, MONTHLY_FORMAT);
	end = time(language)(end, MONTHLY_FORMAT);

	return (
		<Fragment>
			<div
				class={{
					'monthly-view': true,
					'bottom-padding': !requireConfirmation
				}}
			>
				<sn-year-header year={visibleYear.year()} isRangePicker={true} />
				<MonthlyGrid
					dispatch={dispatch}
					updateState={updateState}
					highlightedMonth={highlightedMonth}
					hoveredMonth={hoveredMonth}
					start={start}
					end={end}
					displayedYear={visibleYear.year()}
					language={language}
					isRangePicker={true}
				/>
			</div>
			{requireConfirmation ? pickerConfirm({ dispatch }) : null}
		</Fragment>
	);
};
