import { createCustomElement } from '@servicenow/ui-core';

import view from './view';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';

// Ensure QUARTERLY_VIEW_TAG in src/constants matches elementName below
createCustomElement('sn-controls-quarter-range', {
	view,
	styles,
	properties: {
		start: {
			schema: { type: 'string' }
		},
		end: {
			schema: { type: 'string' }
		},
		language: {
			schema: { type: 'string' },
			default: 'en-US'
		},
		visibleYear: {
			schema: { type: 'string' }
		},
		requireConfirmation: {
			schema: { type: 'boolean' },
			default: false
		}
	},
	initialState: {
		highlightedQuarter: { isValid: () => false }
	},
	actionHandlers
});
