import { time } from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';
import { flow } from 'lodash';
import moment from 'moment';
import { isDateIncluded } from '@servicenow/now-record-mini-calendar';

export const validRange = (
	start,
	end,
	format = 'YYYY-MM-DD',
	language = 'en-US'
) => {
	if (!start && !end) return { valid: true };
	if (start && end) {
		let startDate = time(language)(start, format);
		let endDate = time(language)(end, format);
		if (
			startDate.isValid() &&
			endDate.isValid() &&
			startDate.isSameOrBefore(endDate)
		)
			return {
				valid: true,
				start: startDate.format(format),
				end: endDate.format(format)
			};
	}
	return { valid: false };
};

const invalidFormatError = (inputStr, format) => ({
	type: 'error',
	message: t(
		'Invalid date {0}. Must be a valid date in the format {1}',
		inputStr,
		format
	)
});

const invalidError = {
	type: 'error',
	message: t('Invalid selection. Choose another date.')
};
const invalidRangeError = {
	type: 'error',
	message: t('Invalid Range. The start must be earlier than the end.')
};

const createInvalidationChecker = ({ format, disabledDates }) => {
	return (dateToCheck, initDate, otherDate) => {
		const inValid = !dateToCheck.isValid();
		if (inValid && initDate) return true;
		if (!initDate && otherDate.isValid()) return true;
		if (!inValid && isDateIncluded(dateToCheck, format, disabledDates))
			return true;
		return false;
	};
};

export const getErrors = ({ start, end, language, format, disabledDates }) => {
	let startDate = time(language)(start, format, true);
	let endDate = time(language)(end, format, true);
	let startError;
	let endError;
	if (!startDate.isValid() && start) {
		startError = [invalidFormatError(start, format)];
	}
	if (!endDate.isValid() && end) {
		endError = [invalidFormatError(end, format)];
	}
	//if format is valid, check for other errors
	if (!startError && !endError) {
		const isValidRange = validRange(start, end, format, language).valid;
		const isInvalid = createInvalidationChecker({ format, disabledDates });

		if (isInvalid(startDate, start, endDate)) startError = [invalidError];

		if (isInvalid(endDate, end, startDate)) endError = [invalidError];

		if (startDate.isValid() && endDate.isValid() && !isValidRange) {
			startError = [invalidRangeError];
			endError = [invalidRangeError];
		}
	}
	return {
		startError: startError || [],
		endError: endError || []
	};
};

export const getVisibleMonth = flow(moment, date =>
	date && date.isValid() ? date : moment()
);
