const SET_HOVER_END_DATE = 'DATE_RANGE_PICKER#SET_HOVER_END_DATE';
const SET_END_STATE = 'DATE_RANGE_PICKER#SET_END_STATE';
const SOURCE_FIELD_START = 'DATE_RANGE_PICKER_START';
const TOGGLE_DROPDOWN = 'DATE_PICKER_PANEL_DROPDOWN#OPENED_SET';

export const TRY_CLOSE_DROPDOWN = 'TRY_CLOSE_DROPDOWN';
export const TRY_SET_END_STATE = 'TRY_SET_END_STATE';

export default {
	[TRY_CLOSE_DROPDOWN]: {
		effect: ({ properties: { requireConfirmation }, dispatch }) => {
			if (!requireConfirmation) dispatch(TOGGLE_DROPDOWN, { value: false });
		},
		stopPropagation: true
	},
	[SET_HOVER_END_DATE]: {
		effect: ({ updateState, action }) => {
			const { hoverEnd } = action.payload;
			updateState({ hoverEnd });
		},
		stopPropagation: true
	},
	[TRY_SET_END_STATE]: {
		effect: ({
			properties: { requireConfirmation },
			dispatch,
			action: { payload }
		}) => {
			dispatch(SET_END_STATE, {
				...payload,
				sourceField: requireConfirmation ? SOURCE_FIELD_START : ''
			});
		},
		stopPropagation: true
	}
};
