import { createRef } from '@servicenow/ui-renderer-snabbdom';
import {
	SET_START_STATE,
	SET_END_STATE,
	SET_START_AND_END_STATE,
	SET_VISIBLE_MONTH,
	SET_HOVER_END_DATE,
	TOGGLE_DROPDOWN,
	SET_SOURCE_FIELD,
	SOURCE_FIELD_START,
	SOURCE_FIELD_END,
	SET_HIGHLIGHTED_DATE,
	SET_END
} from '../actionHandlers';
import { getVisibleMonth } from '../dateRangeUtils';
import { getDisabledDays } from '@servicenow/now-record-mini-calendar';
import { TRY_CLOSE_DROPDOWN, TRY_SET_END_STATE } from './actionHandlers';
import '@servicenow/now-dropdown';
import '@servicenow/now-button';
import { isObject } from 'lodash';
import { t } from 'sn-translate';
import { time } from '@devsnc/sn-controls-common';
import pickerConfirm from '../picker-confirm';

let focusControlButton = ref => {
	const controlButton = ref.current.shadowRoot.querySelector('now-button');
	if (controlButton) {
		controlButton.focus();
	}
};

export const onControlExit = (
	dispatch,
	fromRef,
	toRef,
	sourceField
) => event => {
	event.stopPropagation();
	event.preventDefault();
	if (SOURCE_FIELD_START === sourceField) {
		focusControlButton(fromRef);
	} else if (SOURCE_FIELD_END === sourceField) {
		focusControlButton(toRef);
	}
	dispatch(TOGGLE_DROPDOWN, { value: false });
};

let updateEndHoverValue = (dispatch, format) => value => {
	dispatch(SET_HOVER_END_DATE, { hoverEnd: value ? value.format(format) : '' });
};

let onHighlightedDate = (dispatch, visibleMonth, format) => newDate => {
	const vMonth = visibleMonth.month();
	const nMonth = newDate.month();

	if (vMonth - nMonth === 1 || vMonth - nMonth === -11) {
		visibleMonth = visibleMonth.clone().subtract(1, 'month');
	} else if (nMonth - vMonth === 2 || nMonth - vMonth === -10) {
		visibleMonth = visibleMonth.clone().add(1, 'month');
	}

	return dispatch(SET_HIGHLIGHTED_DATE, {
		newDate: newDate.format(format),
		visibleMonth
	});
};

// Selections:
// 1. None selected: `start`
// 2. Start selected:
//   If new selection is after start: `end`
//   If new selection is before start: `start`
// 3. Both selected:
//   Always reset
export let createUpdateDateFunction = ({
	dispatch,
	currentStart,
	currentEnd,
	format,
	language
}) => (sourceField, fromTime = false) => newDate => {
	if (typeof newDate === 'string') {
		newDate = time(language)(newDate, format);
	}

	const getNewDate = (current, next) => {
		let normalizedDate;

		// If there exists a current date + setting the date specifically (not the time),
		// persist the existing time
		if (fromTime) {
			normalizedDate = next;
		} else {
			if (current) {
				let currentTime = time(language)(current, format);

				normalizedDate = next
					.clone()
					.milliseconds(currentTime.milliseconds())
					.seconds(currentTime.seconds())
					.minutes(currentTime.minutes())
					.hours(currentTime.hours());
			} else {
				normalizedDate = next.startOf('day');
			}
		}

		return normalizedDate.format(format);
	};

	const setDate = (editState, payload) => {
		dispatch(editState, {
			...payload,
			newDate: getNewDate(
				editState === SET_START_STATE ? currentStart : currentEnd,
				newDate
			)
		});
	};

	let noneSelected = !currentStart && !currentEnd;
	let onlyStartSelected = !!currentStart && !currentEnd;
	let onlyEndSelected = !currentStart && !!currentEnd;
	let isStartAndEndSelected = !!currentStart && !!currentEnd;
	let payload = { newDate: newDate.format(format) };

	const tryCloseDropdown = () => dispatch(TRY_CLOSE_DROPDOWN);

	if (noneSelected) {
		dispatch(SET_HOVER_END_DATE, { hoverEnd: '' });
		return setDate(SET_START_STATE, {
			...payload,
			sourceField: SOURCE_FIELD_END
		});
	} else if (onlyStartSelected) {
		// only check if new date selected is after currentStart if the end date is being set
		if (!sourceField || SOURCE_FIELD_END === sourceField) {
			if (newDate.isSameOrAfter(time(language)(currentStart, format))) {
				setDate(TRY_SET_END_STATE, payload);
				//close the dropdown
				return tryCloseDropdown();
			}
		} else {
			return setDate(SET_START_STATE, {
				...payload
			});
		}
	} else if (onlyEndSelected) {
		return dispatch(SET_START_AND_END_STATE, {
			start: getNewDate(currentStart, newDate),
			end: '',
			preset: 'custom'
		});
	} else if (isStartAndEndSelected) {
		if (
			SOURCE_FIELD_END === sourceField &&
			newDate.isSameOrAfter(time(language)(currentStart, format))
		) {
			setDate(TRY_SET_END_STATE, payload);
			return tryCloseDropdown();
		} else if (SOURCE_FIELD_START == sourceField && currentStart) {
			dispatch(SET_SOURCE_FIELD, { sourceField: SOURCE_FIELD_END });
			if (newDate.isSameOrBefore(time(language)(currentEnd, format))) {
				return dispatch(SET_START_AND_END_STATE, {
					start: getNewDate(currentStart, newDate),
					end: currentEnd,
					preset: 'custom'
				});
			}
		}
	}

	// both start & end are selected
	dispatch(SET_HOVER_END_DATE, { hoverEnd: '' });

	return dispatch(SET_START_AND_END_STATE, {
		start: getNewDate(currentStart, newDate),
		end: '',
		preset: 'custom'
	});
};

export default function DatePickerPanel(
	{
		hoverEnd,
		properties: {
			name,
			start,
			end,
			visibleMonth,
			format,
			hasTime,
			sourceField,
			highlightedDate,
			fromRef,
			toRef,
			opened,
			dir,
			language,
			weeklyView = false,
			requireConfirmation,
			disabledDates,
			timeAgoLabel,
			timeAgo
		}
	},
	{ dispatch }
) {
	let leftVisibleMonth = visibleMonth
		? visibleMonth.clone()
		: getVisibleMonth(start);
	let rightVisibleMonth = leftVisibleMonth.clone().add(1, 'month');
	let rangePickerRef = createRef();

	let hasFooter = hasTime || requireConfirmation;

	let tableName = '';

	if (isObject(timeAgo)) {
		tableName = timeAgo.tableName;
	}

	const updateDate = createUpdateDateFunction({
		dispatch,
		currentStart: start,
		currentEnd: end,
		format,
		language
	});

	return (
		<div
			className={`panel-container${timeAgo ? ' is-preset' : ''}`}
			ref={rangePickerRef}
		>
			{timeAgo ? (
				<div className="date-preset-container">
					<sn-date-preset-panel-connected
						value={timeAgoLabel}
						tableName={tableName}
						between={false}
						absolute={true}
						hasTime={hasTime}
						format={format}
					/>
				</div>
			) : null}
			<div className="date-picker-container">
				<div className="date-pickers">
					<div className="date-picker-start">
						<sn-standard-month-grid
							visibleMonth={leftVisibleMonth}
							start={start}
							end={end}
							format={format}
							language={language}
							rangePickerRef={rangePickerRef}
							rangePicker={true}
							sourceField={sourceField}
							onSelectDate={updateDate()}
							onControlExit={onControlExit(
								dispatch,
								fromRef,
								toRef,
								sourceField
							)}
							onHighlightedDate={onHighlightedDate(
								dispatch,
								visibleMonth,
								format
							)}
							onChangeMonth={date =>
								dispatch(SET_VISIBLE_MONTH, { visibleMonth: date })
							}
							showAdjacentMonthDays={true}
							onEndDayHover={updateEndHoverValue(dispatch, format)}
							hoverEnd={hoverEnd}
							weeklyView={weeklyView}
							highlightedDate={time(language)(highlightedDate)}
							hasFocus={
								SOURCE_FIELD_START === sourceField ||
								SOURCE_FIELD_END === sourceField
							}
							disabledDays={getDisabledDays(
								leftVisibleMonth,
								format,
								disabledDates
							)}
							enableMonthNavigationChevrons={false}
						/>
					</div>
					<span className="spacer" />
					<div className="date-picker-end">
						<sn-standard-month-grid
							visibleMonth={rightVisibleMonth}
							start={start}
							end={end}
							format={format}
							language={language}
							rangePickerRef={rangePickerRef}
							rangePicker={true}
							sourceField={sourceField}
							onSelectDate={updateDate()}
							onControlExit={onControlExit(
								dispatch,
								fromRef,
								toRef,
								sourceField
							)}
							onHighlightedDate={onHighlightedDate(
								dispatch,
								visibleMonth,
								format
							)}
							onChangeMonth={date =>
								dispatch(SET_VISIBLE_MONTH, {
									visibleMonth: date.subtract(1, 'month')
								})
							}
							showAdjacentMonthDays={true}
							onEndDayHover={updateEndHoverValue(dispatch, format)}
							hoverEnd={hoverEnd}
							weeklyView={weeklyView}
							highlightedDate={time(language)(highlightedDate)}
							hasFocus={
								SOURCE_FIELD_START === sourceField ||
								SOURCE_FIELD_END === sourceField
							}
							disabledDays={getDisabledDays(
								rightVisibleMonth,
								format,
								disabledDates
							)}
							enableMonthNavigationChevrons={true}
						/>
					</div>
				</div>
				{hasFooter ? (
					<div className="date-picker-panel-footer">
						{hasTime ? (
							<div className="time-picker-row">
								<div className="time-picker">
									<now-record-time
										name={`${name}-start-time-picker`}
										label={t('Select Time')}
										format={format}
										value={start}
										on-focus={() =>
											dispatch(SET_SOURCE_FIELD, {
												sourceField: SOURCE_FIELD_START
											})
										}
										onValueChange={({ value }) => {
											value =
												value || time(language)(start, format).startOf('day');
											updateDate(SOURCE_FIELD_START, true)(value);
										}}
									/>
								</div>
								<div className="time-picker">
									<now-record-time
										name={`${name}-end-time-picker`}
										label={t('Select Time')}
										format={format}
										value={end}
										on-focus={() =>
											dispatch(SET_SOURCE_FIELD, {
												sourceField: SOURCE_FIELD_END
											})
										}
										onValueChange={({ value }) => {
											value = value || time(language)(end, format).endOf('day');
											updateDate(SOURCE_FIELD_END, true)(value);
										}}
									/>
								</div>
							</div>
						) : null}
						{requireConfirmation ? pickerConfirm({ dispatch }) : null}
					</div>
				) : null}
			</div>
		</div>
	);
}
