import moment from 'moment';
import { get } from 'lodash';
import { actionTypes } from '@servicenow/ui-core';

import { time } from '@devsnc/sn-controls-common';

import applyPreset from './apply-preset';
import { validRange, getVisibleMonth } from './dateRangeUtils';
import { blurEventEffect } from './eventHandlers';
import {
	WEEKLY_TYPE,
	WEEKLY_FORMAT,
	MONTHLY_TYPE,
	MONTHLY_FORMAT,
	YEARLY_TYPE,
	YEARLY_FORMAT,
	QUARTERLY_TYPE,
	QUARTERLY_FORMAT,
	CUSTOM_PRESET_LABEL
} from './constants';

const COMPONENT_NAME = 'DATE_RANGE_PICKER';
export const SOURCE_FIELD_START = 'DATE_RANGE_PICKER_START';
export const SOURCE_FIELD_END = 'DATE_RANGE_PICKER_END';
export const SET_DATE = `${COMPONENT_NAME}#SET_DATE`;
export const SET_START = `${COMPONENT_NAME}#SET_START`;
export const SET_START_STATE = `${COMPONENT_NAME}#SET_START_STATE`;
export const SET_END = `${COMPONENT_NAME}#SET_END`;
export const SET_END_STATE = `${COMPONENT_NAME}#SET_END_STATE`;
export const SET_START_AND_END = `${COMPONENT_NAME}#SET_START_AND_END`;
export const SET_START_AND_END_STATE = `${COMPONENT_NAME}#SET_START_AND_END_STATE`;
export const SET_PENDING_START_STATE = `${COMPONENT_NAME}#SET_PENDING_START_STATE`;
export const SET_PENDING_END_STATE = `${COMPONENT_NAME}#SET_PENDING_END_STATE`;
export const SET_LAST_VALID_STATE = `${COMPONENT_NAME}#SET_LAST_VALID_STATE`;
export const SET_VISIBLE_MONTH = `${COMPONENT_NAME}#SET_VISIBLE_MONTH`;
export const SET_VISIBLE_YEAR = `${COMPONENT_NAME}#SET_VISIBLE_YEAR`;
export const SET_HOVER_END_DATE = `${COMPONENT_NAME}#SET_HOVER_END_DATE`;
export const SET_SOURCE_FIELD = `${COMPONENT_NAME}#SET_SOURCE_FIELD`;
export const TOGGLE_DROPDOWN = `DATE_PICKER_PANEL_DROPDOWN#OPENED_SET`;
export const SET_HIGHLIGHTED_DATE = `${COMPONENT_NAME}#SET_HIGHLIGHTED_DATE`;
export const CONFIRM_FIELDS = ['confirm-ok', 'confirm-cancel'];
const BUTTON_CLICK = `NOW_BUTTON#CLICKED`;
const PRESET_MESSAGE_UPDATED = 'SN_DATE_PRESET_PANEL#MESSAGE_UPDATED';
const PRESET_VALUE_CHANGED = 'SN_DATE_PRESET_PANEL#VALUE_CHANGED';

const createPayload = ({
	start,
	end,
	startError,
	endError,
	state: { format }
}) => {
	const isoStart = moment(start, format);
	const isoEnd = moment(end, format);

	return {
		isoStart: isoStart.isValid() ? isoStart.toISOString() : '',
		isoEnd: isoEnd.isValid() ? isoEnd.toISOString() : '',
		start,
		end,
		startError,
		endError
	};
};

export let createDispatchValueEffect = (createPayload, actionToDispatch) => ({
	effect: ({ action, dispatch, state }) => {
		let { format } = state;
		let { onValueChange, name, language } = state.properties;

		let payload = {
			name,
			...createPayload({ ...action.payload, state })
		};

		let lastValidRange = state.lastValidRange;
		let { start, end } = payload;

		const isNewRange =
			start !== lastValidRange.start || end !== lastValidRange.end;

		if (isNewRange) {
			let newRange = validRange(start, end, format, language);

			if (newRange.valid) {
				dispatch(SET_LAST_VALID_STATE, { ...newRange, preset: state.preset });

				if (onValueChange) {
					onValueChange(payload);
				} else {
					dispatch('NOW_DATE_RANGE_PICKER#VALUE_CHANGED', payload);
				}
			}
		}
		dispatch(actionToDispatch, action.payload);
	},
	stopPropagation: true
});

const getDate = (dateString, format, language) => {
	if (dateString) {
		let date = time(language)(dateString, format, true);
		return date && date.isValid() ? date.format(format) : '';
	}
	return '';
};

const getInitialState = (state, properties, propertyChanged) => {
	let { start, end, format, language, preset, presets } = properties;

	switch (properties.type) {
		case WEEKLY_TYPE:
			format = WEEKLY_FORMAT;
			break;
		case MONTHLY_TYPE:
			format = MONTHLY_FORMAT;
			break;
		case YEARLY_TYPE:
			format = YEARLY_FORMAT;
			break;
		case QUARTERLY_TYPE:
			format = QUARTERLY_FORMAT;
			break;
		default:
			format =
				format || (properties.hasTime ? 'YYYY-MM-DD hh:mm:ss a' : 'YYYY-MM-DD');
	}

	let pendingStart = (start = getDate(start, format, language));
	let pendingEnd = (end = getDate(end, format, language));
	let visibleMonth = getVisibleMonth(start, format);

	let lastValidRange = {
		start,
		end,
		preset
	};

	if (preset !== 'custom' && !propertyChanged) {
		let { start: newStart, end: newEnd } = applyPreset({
			date: time(language)(),
			presets,
			preset,
			format
		});

		pendingStart = start = newStart;
		pendingEnd = end = newEnd;

		visibleMonth = getVisibleMonth(start, format);
		lastValidRange = {
			start,
			end,
			preset
		};
	}

	if (!validRange(start, end, format, language).valid) {
		// not sure if defaulting to blank is the best approach or
		// if the date picker should have some sort of error state
		start = end = '';
		preset = 'custom';
		lastValidRange = {
			start: '',
			end: '',
			preset: 'custom'
		};
	}

	return {
		...state,
		start,
		end,
		pendingStart,
		pendingEnd,
		preset,
		visibleMonth,
		lastValidRange,
		requireConfirmation: properties.requireConfirmation || properties.hasTime,
		format,
		timeAgoLabel: CUSTOM_PRESET_LABEL
	};
};

const getInitialStateEffect = propertyChanged => ({
	properties,
	state,
	updateState
}) => {
	updateState(getInitialState(state, properties, propertyChanged));
};

// When setting start/end date:
// 1. Dispatch to consumer that it's been updated
// 2. Update internal state
export default {
	[actionTypes.COMPONENT_CONNECTED]: getInitialStateEffect(false),
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: getInitialStateEffect(true),
	[SET_DATE]: {
		// Trying to consolidate the logic from updateDate in date-picker-panel here
		effect: ({
			dispatch,
			action: {
				payload: { date }
			},
			properties: { language },
			state: { format, sourceField, start, end, requireConfirmation }
		}) => {
			let currentStart = time(language)(start, format);
			let currentEnd = time(language)(end, format);

			let formattedDate = date.startOf('day').format(format);

			if (!currentStart.isValid() || currentStart.isAfter(date)) {
				dispatch(SET_START_STATE, {
					newDate: formattedDate,
					sourceField: SOURCE_FIELD_END
				});

				return;
			}

			if (!currentEnd.isValid()) {
				dispatch(SET_END_STATE, {
					newDate: formattedDate,
					sourceField: ''
				});

				requireConfirmation || dispatch(TOGGLE_DROPDOWN, { value: false });

				return;
			}

			const editingStart = SOURCE_FIELD_START === sourceField;
			const editingEnd = SOURCE_FIELD_END === sourceField;

			// Start + End are set and valid.
			if (editingEnd && date.isSameOrAfter(currentStart)) {
				dispatch(SET_END_STATE, {
					newDate: formattedDate,
					sourceField: requireConfirmation ? SOURCE_FIELD_START : ''
				});
				requireConfirmation || dispatch(TOGGLE_DROPDOWN, { value: false });
			} else if (editingStart) {
				if (date.isSameOrBefore(currentEnd)) {
					return dispatch(SET_START_STATE, {
						newDate: formattedDate,
						sourceField: SOURCE_FIELD_END
					});
				} else {
					return dispatch(SET_START_AND_END_STATE, {
						start: formattedDate,
						end: '',
						sourceField: ''
					});
				}
			}
		}
	},
	[SET_START_STATE]: {
		effect: ({ updateState, action }) => {
			const { newDate, sourceField } = action.payload;
			updateState({
				start: newDate,
				pendingStart: newDate,
				highlightedDate: newDate,
				sourceField,
				timeAgoLabel: CUSTOM_PRESET_LABEL,
				preset: 'custom' // always reset to custom b/c presets use SET_START_AND_END
			});
		},
		stopPropagation: true
	},
	[SET_END_STATE]: {
		effect: ({ updateState, action }) => {
			const { newDate, sourceField } = action.payload;
			updateState({
				end: newDate,
				pendingEnd: newDate,
				sourceField,
				timeAgoLabel: CUSTOM_PRESET_LABEL,
				preset: 'custom' // always reset to custom b/c presets use SET_START_AND_END
			});
		},
		stopPropagation: true
	},
	[SET_START_AND_END]: createDispatchValueEffect(
		createPayload,
		SET_START_AND_END_STATE
	),
	[SET_START_AND_END_STATE]: {
		effect: ({
			updateState,
			action,
			state: { sourceField: currentSourceField }
		}) => {
			const {
				start,
				startError,
				end,
				endError,
				preset,
				sourceField = currentSourceField
			} = action.payload;
			updateState({
				start,
				startError,
				end,
				endError,
				pendingStart: start,
				pendingEnd: end,
				preset,
				timeAgoLabel: CUSTOM_PRESET_LABEL,
				sourceField
			});
		},
		stopPropagation: true
	},
	[SET_PENDING_START_STATE]: {
		effect: ({ updateState, action }) => {
			const { newDate } = action.payload;
			updateState({
				pendingStart: newDate
			});
		},
		stopPropagation: true
	},
	[SET_PENDING_END_STATE]: {
		effect: ({ updateState, action }) => {
			const { newDate } = action.payload;
			updateState({
				pendingEnd: newDate
			});
		},
		stopPropagation: true
	},
	[SET_LAST_VALID_STATE]: {
		effect: ({ updateState, action }) => {
			const { start, end, preset } = action.payload;
			updateState({
				lastValidRange: {
					start,
					end,
					preset
				}
			});
		},
		stopPropagation: true
	},
	[SET_VISIBLE_MONTH]: {
		effect: ({ updateState, action }) => {
			const { visibleMonth } = action.payload;
			updateState({
				visibleMonth,
				highlightedDate: moment(visibleMonth).startOf('month')
			});
		},
		stopPropagation: true
	},
	[SET_VISIBLE_YEAR]: {
		effect: ({
			updateState,
			action: {
				payload: { year }
			},
			state: { visibleMonth }
		}) => {
			updateState({
				visibleMonth: moment(visibleMonth).year(year)
			});
		},
		stopPropagation: true
	},
	[SET_SOURCE_FIELD]: {
		effect: ({ updateState, action }) => {
			const { sourceField } = action.payload;
			updateState({
				sourceField
			});
		},
		stopPropagation: true
	},
	[BUTTON_CLICK]: {
		effect: ({ action: { payload }, dispatch }) => {
			const field = get(payload, 'field');
			if (CONFIRM_FIELDS.includes(field))
				dispatch(TOGGLE_DROPDOWN, {
					value: false,
					cancel: field === 'confirm-cancel'
				});
		},
		stopPropagation: true
	},
	[TOGGLE_DROPDOWN]: {
		effect: ({ action, dispatch, state, properties, updateState }) => {
			let { value, sourceField, cancel } = action.payload;

			// if closing, do the value change
			if (!value) {
				blurEventEffect({ dispatch, state, properties, cancel });
			}

			updateState({
				popoverOpened: value,
				originalStart: state.start,
				originalEnd: state.end
			});
			dispatch(SET_SOURCE_FIELD, { sourceField });
		},
		stopPropagation: true
	},
	[SET_HIGHLIGHTED_DATE]: {
		effect: ({ updateState, action }) => {
			const { newDate, visibleMonth } = action.payload;
			updateState({
				highlightedDate: newDate,
				visibleMonth
			});
		},
		stopPropagation: true
	},
	'NOW_POPOVER#OPENED_SET': {
		effect: ({ action, dispatch }) => {
			//DEF0119521: Handle Closing of the Popover only when
			//events (escape) are on the date range picker popover and not on its underlying components
			//like now-dropdown or such
			const { id, value } = action.payload;

			//check only date range picker popover
			if (id === 'date-range-picker-popover') {
				dispatch(TOGGLE_DROPDOWN, { value });
			}
		},
		stopPropagation: true
	},
	[PRESET_MESSAGE_UPDATED]: {
		effect: ({ properties: { messages = [] }, action, updateProperties }) => {
			const message = action.payload;
			if (message) {
				updateProperties({
					messages: [...messages, message]
				});
			}
		}
	},
	[PRESET_VALUE_CHANGED]: {
		effect: ({
			properties: { language, requireConfirmation, hasTime },
			state: { format },
			action,
			dispatch,
			updateState
		}) => {
			const start = get(action, 'payload.absolute.start');
			const end = get(action, 'payload.absolute.end');
			const label = get(action, 'payload.label');
			const absoluteStart = time(language)(start);
			const absoluteEnd = time(language)(end);
			const hasFooter = hasTime || requireConfirmation;

			if (absoluteStart.isValid() && absoluteEnd.isValid()) {
				const visibleMonth = getVisibleMonth(absoluteStart, format);
				const formattedStart = absoluteStart.format(format);
				const formattedEnd = absoluteEnd.format(format);
				updateState({
					pendingStart: formattedStart,
					pendingEnd: formattedEnd,
					start: formattedStart,
					end: formattedEnd,
					visibleMonth,
					timeAgoLabel: label
				});
				if (!hasFooter) {
					dispatch(TOGGLE_DROPDOWN, { value: false });
				}
			}
		}
	}
};
