import { createCustomElement } from '@servicenow/ui-core';

import view from './view';
import styles from './styles.scss';
import actionHandlers, { INIT_HIGHLIGHTED_YEAR } from './actionHandlers';

// Ensure YEARLY_VIEW_TAG in src/constants matches elementName below
createCustomElement('sn-controls-year-range', {
	view,
	styles,
	properties: {
		start: {
			schema: { type: 'string' }
		},
		end: {
			schema: { type: 'string' }
		},
		visibleYear: {
			schema: { type: 'string' }
		},
		popoverOpened: {
			schema: { type: 'boolean' }
		},
		requireConfirmation: {
			schema: { type: 'boolean' },
			default: false
		}
	},
	initialState: {
		highlightedYear: INIT_HIGHLIGHTED_YEAR
	},
	actionHandlers
});
