import { createRef } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-popover';
import { getView } from './viewFactory';
import { isAttrTrue } from '@devsnc/sn-controls-common';

import {
	SET_START_STATE,
	SET_END_STATE,
	SET_PENDING_START_STATE,
	SET_PENDING_END_STATE,
	SOURCE_FIELD_START,
	SOURCE_FIELD_END,
	SET_VISIBLE_MONTH,
	TOGGLE_DROPDOWN
} from './actionHandlers';

import '@devsnc/sn-record-input';
import './date-picker-panel';
import '@devsnc/sn-record-control';
import { t } from 'sn-translate';
import makeClass from 'classnames';
import { time } from '@devsnc/sn-controls-common';

const createIsValidTime = (format, language) => date =>
	time(language)(date, format, true).isValid();

export default function DateRangePicker(state, { dispatch }) {
	const {
		start,
		startError,
		end,
		endError,
		format,
		properties: { labelStart, labelEnd, reversed, language, readonly },
		sourceField,
		pendingStart,
		pendingEnd,
		popoverOpened
	} = state;
	const isReadonly = isAttrTrue(readonly);
	const isValidTime = createIsValidTime(format, language);
	let fromRef = createRef();
	let toRef = createRef();
	let pickerRef = createRef();
	let toggle = field => e => {
		e.stopPropagation();
		const value = !sourceField;

		dispatch(TOGGLE_DROPDOWN, { value: !popoverOpened, sourceField: field });

		if (value) {
			dispatch(SET_VISIBLE_MONTH, {
				visibleMonth:
					start && isValidTime(start)
						? time(language)(start, format)
						: time(language)()
			});
		} else {
			field = '';
		}
	};

	const placeholder = !readonly ? format : null;

	return (
		<now-popover
			hide-tail
			interaction-type="dialog"
			opened={popoverOpened}
			manage-opened
			positions={[
				{ target: 'bottom-start', content: 'top-start' },
				{ target: 'top-start', content: 'bottom-start' },
				{ target: 'bottom-end', content: 'top-end' },
				{ target: 'top-end', content: 'bottom-end' }
			]}
			constrain={{
				maxWidth: 765
			}}
			offset={2}
			append-to-payload={{ id: 'date-range-picker-popover' }}
		>
			<div
				className="date-input-wrapper"
				slot="trigger"
				// allow to be programatically focused on by now-popover when closed
				tabindex="-1"
				on-click={e => {
					// Prevent it from propagating to now-popover's slot.onClick which dispatches OPENED_SET
					e.stopPropagation();
				}}
			>
				<sn-record-input
					className={makeClass('sn-record-input', {
						'is-focused': sourceField === SOURCE_FIELD_START
					})}
					type="text"
					{...(SOURCE_FIELD_END === sourceField ? { tabindex: '-1' } : {})}
					label={labelStart}
					name={SOURCE_FIELD_START}
					placeholder={placeholder}
					value={pendingStart}
					messages={startError}
					onValueChange={({ value }) =>
						dispatch(SET_PENDING_START_STATE, { newDate: value })
					}
					onBlur={e => {
						if (isReadonly) {
							e.stopPropagation();
						} else if (
							(isValidTime(pendingStart) || !pendingStart) &&
							pendingStart !== start
						) {
							dispatch(SET_START_STATE, {
								newDate: pendingStart,
								sourceField: ''
							});
						}
					}}
					reversed={reversed}
					readonly={isReadonly}
					invalid={startError && startError.length > 0 ? true : false}
				>
					{!isReadonly ? (
						<span slot="controls">
							<sn-record-control
								ref={fromRef}
								icon="calendar-outline"
								on-click={toggle(SOURCE_FIELD_START)}
								tooltip={t('Show calendar')}
								size="sm"
								variant="tertiary"
								bare={true}
								iconic={true}
								config-aria={{
									'aria-label': t('Select Date Range Start'),
									'aria-expanded': (
										SOURCE_FIELD_START === sourceField
									).toString()
								}}
							/>
						</span>
					) : null}
				</sn-record-input>
				<sn-record-input
					className={makeClass('sn-record-input', {
						'is-focused': sourceField === SOURCE_FIELD_END
					})}
					type="text"
					{...(SOURCE_FIELD_START === sourceField ? { tabindex: '-1' } : {})}
					label={labelEnd}
					name={SOURCE_FIELD_END}
					placeholder={placeholder}
					value={pendingEnd}
					messages={endError}
					onValueChange={({ value }) =>
						dispatch(SET_PENDING_END_STATE, { newDate: value })
					}
					onBlur={e => {
						if (isReadonly) {
							e.stopPropagation();
						} else if (
							(isValidTime(pendingEnd) || !pendingEnd) &&
							pendingEnd !== end
						) {
							dispatch(SET_END_STATE, { newDate: pendingEnd });
						}
					}}
					reversed={reversed}
					readonly={isReadonly}
					invalid={endError && endError.length > 0 ? true : false}
				>
					{!isReadonly ? (
						<span slot="controls">
							<sn-record-control
								ref={toRef}
								icon="calendar-outline"
								on-click={
									pendingEnd
										? toggle(SOURCE_FIELD_END)
										: toggle(SOURCE_FIELD_START)
								}
								tooltip={t('Show calendar')}
								size="sm"
								variant="tertiary"
								bare={true}
								iconic={true}
								config-aria={{
									'aria-label': t('Select Date Range End'),
									'aria-expanded': (SOURCE_FIELD_END === sourceField).toString()
								}}
							/>
						</span>
					) : null}
				</sn-record-input>
			</div>
			{getView(
				{
					...state,
					start: isValidTime(start) ? start : '',
					end: isValidTime(end) ? end : ''
				},
				{ toRef, pickerRef, fromRef }
			)}
		</now-popover>
	);
}
