import moment from 'moment';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { Yearly } from '@servicenow/now-record-mini-calendar';
import { YEARLY_FORMAT } from '../constants';
import pickerConfirm from '../picker-confirm';

const {
	components: { YearlyHeader, YearlyGrid }
} = Yearly;
const INVALID_MOMENT = moment(null);

export default (
	{
		highlightedYear = INVALID_MOMENT,
		hoveredYear = INVALID_MOMENT,
		properties: { start, end, visibleYear, requireConfirmation }
	},
	{ dispatch, updateState }
) => {
	if (!visibleYear) {
		visibleYear = moment(start || moment()).year();
		updateState({ visibleYear, shouldRender: false });
	}
	start = moment(start, YEARLY_FORMAT);
	end = moment(end, YEARLY_FORMAT);

	return (
		<Fragment>
			<div
				class={{
					'yearly-view': true,
					'bottom-padding': !requireConfirmation
				}}
			>
				<YearlyHeader />
				<YearlyGrid
					dispatch={dispatch}
					updateState={updateState}
					highlightedYear={highlightedYear}
					hoveredYear={hoveredYear}
					displayedYear={visibleYear}
					start={start}
					end={end}
				/>
			</div>
			{requireConfirmation ? pickerConfirm({ dispatch }) : null}
		</Fragment>
	);
};
