import {
	STANDARD_TYPE,
	WEEKLY_TYPE,
	STANDARD_VIEW_TAG,
	MONTHLY_TYPE,
	MONTHLY_VIEW_TAG,
	YEARLY_TYPE,
	YEARLY_VIEW_TAG,
	QUARTERLY_TYPE,
	QUARTERLY_VIEW_TAG
} from './constants';
import './monthly-view';
import './yearly-view';
import './quarterly-view';
import pickerConfirm from './picker-confirm';

/**
 * Returns daily and weekly view
 */
const daily = (state, additionalProps) => {
	const {
		properties: {
			type,
			name,
			hasTime,
			presets,
			onValueChange,
			language,
			disabledDates,
			timeAgo
		},
		start,
		end,
		visibleMonth,
		preset,
		format,
		requireConfirmation,
		sourceField,
		highlightedDate,
		timeAgoLabel
	} = state;
	const { toRef, pickerRef, fromRef } = additionalProps;

	return (
		<STANDARD_VIEW_TAG
			slot="content"
			name={name}
			start={start}
			fromRef={fromRef}
			toRef={toRef}
			ref={pickerRef}
			end={end}
			visibleMonth={visibleMonth}
			format={format}
			presets={presets}
			sourceField={sourceField}
			preset={preset}
			onValueChange={onValueChange}
			weeklyView={type === WEEKLY_TYPE}
			highlightedDate={highlightedDate}
			language={language}
			requireConfirmation={requireConfirmation}
			hasTime={hasTime}
			disabledDates={disabledDates}
			timeAgoLabel={timeAgoLabel}
			timeAgo={type === WEEKLY_TYPE ? false : timeAgo}
		/>
	);
};

/**
 * Returns monthly view
 */
const monthly = ({
	start,
	end,
	visibleMonth,
	language,
	properties: { requireConfirmation }
}) => {
	return (
		<MONTHLY_VIEW_TAG
			slot="content"
			start={start}
			end={end}
			visibleYear={visibleMonth && String(visibleMonth.year())}
			language={language}
			requireConfirmation={requireConfirmation}
		/>
	);
};

/**
 * Returns yearly view
 */
const yearly = ({
	start,
	end,
	popoverOpened,
	visibleMonth,
	language,
	properties: { requireConfirmation }
}) => {
	return (
		<YEARLY_VIEW_TAG
			slot="content"
			start={start}
			end={end}
			visibleYear={visibleMonth && String(visibleMonth.year())}
			language={language}
			popoverOpened={popoverOpened}
			requireConfirmation={requireConfirmation}
		/>
	);
};

/**
 * Returns quarterly view
 */
const quarterly = ({
	start,
	end,
	visibleMonth,
	properties: { requireConfirmation }
}) => {
	return (
		<QUARTERLY_VIEW_TAG
			start={start}
			end={end}
			slot="content"
			visibleYear={visibleMonth && String(visibleMonth.year())}
			requireConfirmation={requireConfirmation}
		/>
	);
};

export function getView(state, additionalProps) {
	const type = state.properties.type;
	switch (type) {
		case WEEKLY_TYPE:
		case STANDARD_TYPE:
			return daily(state, additionalProps);
		case MONTHLY_TYPE:
			return monthly(state);
		case YEARLY_TYPE:
			return yearly(state);
		case QUARTERLY_TYPE:
			return quarterly(state);
		default:
			return null;
	}
}
