import { SET_START_AND_END } from './actionHandlers';
import { getErrors } from './dateRangeUtils';
import { time } from '@devsnc/sn-controls-common';
import { onControlExit } from './date-picker-panel/view';
import { get } from 'lodash';
import {
	WEEKLY_TYPE,
	STANDARD_VIEW_TAG,
	MONTHLY_VIEW_TAG,
	YEARLY_VIEW_TAG,
	QUARTERLY_VIEW_TAG
} from './constants';
import { isDate } from 'moment';
import { t } from 'sn-translate';

const isDateValidNotEmpty = date => {
	return date.isValid() && date.toString() !== t('Invalid date');
};

// Due to the way that now-popover hoists elements, it'll cause
// an immediate blur from the element. We need to make sure
// the blur event is actually from a blur rather than
// the focus moving to the hoisted content
const isFocusOnHoistedElement = action => {
	const relatedTarget = get(action, 'meta.event.relatedTarget');

	if (!relatedTarget || relatedTarget.tagName !== 'SEISMIC-HOIST') return false;

	const targetFor = relatedTarget.getAttribute('target-for');
	const regex = new RegExp(
		`${STANDARD_VIEW_TAG}|${MONTHLY_VIEW_TAG}|${YEARLY_VIEW_TAG}|${QUARTERLY_VIEW_TAG}`
	);
	return targetFor.match(regex);
};

export const blurEventEffect = ({
	action,
	dispatch,
	state,
	properties,
	cancel
}) => {
	if (isFocusOnHoistedElement(action)) return;

	let { format, requireConfirmation } = state;
	let { language, disabledDates = [] } = properties;

	const originalStart = time(language)(state.originalStart, format, true);
	const originalEnd = time(language)(state.originalEnd, format, true);
	if (
		state.popoverOpened &&
		(cancel || (cancel === undefined && requireConfirmation))
	) {
		dispatch(SET_START_AND_END, {
			start: originalStart.isValid() ? originalStart.format(format) : '',
			end: originalEnd.isValid() ? originalEnd.format(format) : '',
			preset: state.preset,
			...getErrors({
				start: state.originalStart,
				end: state.originalEnd,
				language,
				format,
				disabledDates
			})
		});
		return;
	}

	const pendingStart = state.pendingStart;
	const pendingEnd = state.pendingEnd;
	let startDate = time(language)(pendingStart, format, true);
	let endDate = time(language)(pendingEnd, format, true);

	const isWeekly = properties.type === WEEKLY_TYPE;

	//dispatch with formatted
	dispatch(SET_START_AND_END, {
		start:
			isDateValidNotEmpty(startDate) && !isWeekly
				? startDate.format(format)
				: pendingStart,
		end:
			isDateValidNotEmpty(endDate) && !isWeekly
				? endDate.format(format)
				: pendingEnd,
		preset: state.preset,
		...getErrors({
			start: pendingStart,
			end: pendingEnd,
			language,
			format,
			disabledDates
		})
	});
};

export default [
	{
		events: ['blur'],
		effect: blurEventEffect
	},
	{
		events: ['keydown'],
		effect: ({
			dispatch,
			action: {
				payload: { event }
			},
			properties: { sourceField, toRef, fromRef }
		}) => {
			if (event.key === 'Escape')
				onControlExit(dispatch, fromRef, toRef, sourceField)(event);
		}
	}
];
