import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { time } from '@devsnc/sn-controls-common';
import { QuarterlyGrid } from '@servicenow/now-record-mini-calendar';

import { QUARTERLY_FORMAT } from '../constants';
import pickerConfirm from '../picker-confirm';

export default (
	{
		highlightedQuarter,
		hoveredQuarter,
		properties: {
			language,
			start,
			end,
			visibleYear = start || time(language)(),
			requireConfirmation
		}
	},
	{ dispatch, updateState }
) => {
	visibleYear = time(language)(
		visibleYear,
		visibleYear === start ? QUARTERLY_FORMAT : 'YYYY'
	);
	start = time(language)(start, QUARTERLY_FORMAT);
	end = time(language)(end, QUARTERLY_FORMAT);
	return (
		<Fragment>
			<div
				class={{
					'quarterly-view': true,
					'bottom-padding': !requireConfirmation
				}}
			>
				<sn-year-header year={visibleYear.year()} isRangePicker={true} />
				<QuarterlyGrid
					dispatch={dispatch}
					updateState={updateState}
					start={start}
					end={end}
					displayedYear={visibleYear.year()}
					language={language}
					highlightedQuarter={highlightedQuarter}
					hoveredQuarter={hoveredQuarter}
					isRangePicker={true}
				/>
			</div>
			{requireConfirmation ? pickerConfirm({ dispatch }) : null}
		</Fragment>
	);
};
