import moment from 'moment';
import { QUARTERLY_FORMAT, GRID_EVENTS } from '../constants';
import { actionTypes } from '@servicenow/ui-core';
import { SET_DATE, SET_VISIBLE_YEAR } from '../actionHandlers';

const POPOVER_OPENED_FIELD = 'popoverOpened';

export default {
	[GRID_EVENTS.INIT_HIGHLIGHTED_DATE]: {
		effect: ({
			updateState,
			state: { highlightedQuarter: currentHighlightedQuarter },
			properties: { format = '[Q]Q YYYY', start, visibleYear }
		}) => {
			// Set highlighted quarter in this priority order:
			// 1. Existing highlighted
			// 2. Start date
			// 3. Current date
			const highlightedQuarter =
				currentHighlightedQuarter && currentHighlightedQuarter.isValid()
					? currentHighlightedQuarter
					: start
					? moment(start, format)
					: moment();

			const highlightedYear = highlightedQuarter.year();

			// If the date from above is not visible based on the visibleYear, default to first quarter
			const highlightedDateNotVisible =
				highlightedYear != Number(visibleYear) &&
				highlightedYear != Number(visibleYear) + 1;

			updateState({
				highlightedQuarter: highlightedDateNotVisible
					? moment(`1 ${visibleYear}`, QUARTERLY_FORMAT)
					: highlightedQuarter
			});
		},
		stopPropagation: true
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		effect: ({ action, updateState }) => {
			const { name, value } = action.payload;
			if (name === POPOVER_OPENED_FIELD && !value) {
				updateState({ highlightedQuarter: { isValid: () => false } });
			}
		}
	},

	[GRID_EVENTS.SET_DATE]: {
		effect: ({ action, dispatch }) => {
			dispatch(SET_DATE, { date: action.payload.date });
		},
		stopPropagation: true
	},
	[GRID_EVENTS.SET_VISIBLE_YEAR]: {
		effect: ({ action, dispatch }) => {
			dispatch(SET_VISIBLE_YEAR, { year: action.payload.year });
		},
		stopPropagation: true
	}
};
