import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';

import view from './view';
import actionHandlers from './actionHandlers';
import styles from './panel.scss';

import { onControlExit } from './view';

// Ensure STANDARD_VIEW_TAG in src/constants matches elementName below
createCustomElement('sn-controls-standard-range', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		start: { default: '' },
		end: { default: '' },
		highlightedDate: { default: '' },
		visibleMonth: {},
		format: {},
		hasTime: { default: false, type: 'boolean' },
		name: { default: '' },
		sourceField: {},
		presets: {},
		preset: {},
		opened: { default: false },
		onValueChange: {},
		fromRef: {},
		toRef: {},
		language: {},
		weeklyView: { default: false },
		requireConfirmation: { default: false },
		disabledDates: {},
		rangePicker: {},
		timeAgoLabel: { default: '' },
		/**
		 * Toggles whether or not to show the TimeAgo Preset Panel
		 *
		 * Passing in false will hide the panel altogether.
		 * Passing in true will show the panel with default config which is {tableName: ''}.
		 * Passing in a configObject will show the panel with the config as passed in.

		 * tableName - tableName for the Business Calendar Relative fields.
		 * 
		 * @type {(boolean|{ tableName: string })}
		 *
		 */
		timeAgo: {
			default: false,
			schema: {
				oneOf: [
					{ type: 'boolean' },
					{
						type: 'object',
						properties: {
							tableName: { type: 'string' }
						}
					}
				]
			}
		}
	},
	behaviors: [{ behavior: rtlBehavior }],
	actionHandlers,
	styles,
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({
				dispatch,
				action: {
					payload: { event }
				},
				properties: { sourceField, toRef, fromRef }
			}) => {
				if (event.key === 'Escape')
					onControlExit(dispatch, fromRef, toRef, sourceField)(event);
			}
		}
	]
});
