import { inRange } from 'lodash';
import moment from 'moment';

import { actionTypes } from '@servicenow/ui-core';

import { SET_DATE, SET_VISIBLE_YEAR, CONFIRM_FIELDS } from '../actionHandlers';
import { YEARLY_FORMAT, GRID_EVENTS } from '../constants';

export const INIT_HIGHLIGHTED_YEAR = { isValid: () => false };

const POPOVER_OPENED_FIELD = 'popoverOpened';

export default {
	[GRID_EVENTS.INIT_HIGHLIGHTED_DATE]: {
		effect: ({
			updateState,
			state: { highlightedYear: currentHighlightedDate },
			properties: { start },
			action: {
				payload: {
					visibleRange: { lowerYear, upperYear }
				}
			}
		}) => {
			// Set highlighted year in this priority order:
			// 1. Existing highlighted
			// 2. Start date
			// 3. Current date
			const highlightedDate =
				currentHighlightedDate && currentHighlightedDate.isValid()
					? currentHighlightedDate
					: start
					? moment(start, YEARLY_FORMAT)
					: moment();

			const highlightedYear = highlightedDate.year();

			// If the date from above is not visible based on the visible range, default to lower year
			const highlightedDateVisible = inRange(
				highlightedYear,
				lowerYear,
				upperYear
			);

			updateState({
				highlightedYear: highlightedDateVisible
					? highlightedDate
					: moment({ year: lowerYear })
			});
		},
		stopPropagation: true
	},
	'NOW_BUTTON#CLICKED': {
		effect: ({
			dispatch,
			action: {
				payload: { action, field },
				stopPropagation
			},
			properties: { visibleYear }
		}) => {
			if (CONFIRM_FIELDS.includes(field)) return; // Don't stopPropagation this is for confirm ok/cancel
			const newYear =
				action === 'current' ? moment().year() : Number(visibleYear) + action;

			dispatch(SET_VISIBLE_YEAR, { year: newYear });
			stopPropagation();
		}
	},
	[GRID_EVENTS.SET_DATE]: {
		effect: ({ action, dispatch }) => {
			dispatch(SET_DATE, { date: action.payload.date });
		},
		stopPropagation: true
	},
	[GRID_EVENTS.SET_VISIBLE_YEAR]: {
		effect: ({ action, dispatch }) => {
			dispatch(SET_VISIBLE_YEAR, { year: action.payload.year });
		},
		stopPropagation: true
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		effect: ({ action, updateState }) => {
			const { name, value } = action.payload;
			if (name === POPOVER_OPENED_FIELD && !value) {
				updateState({ highlightedYear: { isValid: () => false } });
			}
		}
	}
};
