import { actionTypes } from '@servicenow/ui-core';
import moment from 'moment';

import { GRID_EVENTS } from '../constants';
import { SET_DATE, SET_VISIBLE_YEAR } from '../actionHandlers';

const POPOVER_OPENED_FIELD = 'popoverOpened';

export default {
	[GRID_EVENTS.INIT_HIGHLIGHTED_DATE]: {
		effect: ({
			updateState,
			state: { highlightedMonth: currentHighlightedMonth },
			properties: { format, start, visibleYear }
		}) => {
			// Set highlighted month in this priority order:
			// 1. Existing highlighted
			// 2. Start date
			// 3. Current date
			const highlightedMonth =
				currentHighlightedMonth && currentHighlightedMonth.isValid()
					? currentHighlightedMonth
					: start
					? moment(start, format)
					: moment();

			const highlightedYear = highlightedMonth.year();

			// If the date from above is not visible based on the visibleYear, default to first month
			const highlightedDateNotVisible =
				highlightedYear != Number(visibleYear) &&
				highlightedYear != Number(visibleYear) + 1;

			updateState({
				highlightedMonth: highlightedDateNotVisible
					? moment({ year: visibleYear, month: 0 })
					: highlightedMonth
			});
		},
		stopPropagation: true
	},
	[GRID_EVENTS.SET_DATE]: {
		effect: ({ action, dispatch }) => {
			dispatch(SET_DATE, { date: action.payload.date });
		},
		stopPropagation: true
	},
	[GRID_EVENTS.SET_VISIBLE_YEAR]: {
		effect: ({ action, dispatch }) => {
			dispatch(SET_VISIBLE_YEAR, { year: action.payload.year });
		},
		stopPropagation: true
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		effect: ({ action, updateState }) => {
			const { name, value } = action.payload;
			if (name === POPOVER_OPENED_FIELD && !value) {
				updateState({ highlightedMonth: { isValid: () => false } });
			}
		}
	}
};
