import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';

import view from './view';
import styles from './styles.scss';
import eventHandlers from './eventHandlers';
import actionHandlers from './actionHandlers';

import './date-picker-panel';

let defaultPresets = [
	{
		id: 'custom',
		label: t('Custom')
	},
	{
		id: 'last 7 days',
		label: t('Last 7 Days'),
		start: {
			offset: '-7',
			unit: 'days'
		}
	},
	{
		id: 'last month',
		label: t('Last Month'),
		start: {
			offset: '-1',
			unit: 'months'
		}
	},
	{
		id: 'last 3 months',
		label: t('Last 3 Months'),
		start: {
			offset: '-3',
			unit: 'months'
		}
	},
	{
		id: 'last 6 months',
		label: t('Last 6 Months'),
		start: {
			offset: '-6',
			unit: 'months'
		}
	},
	{
		id: 'year to date',
		label: t('Year To Date'),
		start: {
			absolute: true,
			method: 'startOf',
			value: 'year'
		}
	},
	{
		id: 'last year',
		label: t('Last Year'),
		start: {
			offset: '-1',
			unit: 'year'
		}
	}
];

/**
 * Component used to select a custom range of dates or select from a preset of date ranges.
 *
 * ```jsx
 * <now-date-range-picker start="10-01-2005" end="11-03-2005" preset="custom "/>
 *
 * // pass only relative preset
 * <now-date-range-picker preset="last month"/>
 *
 * // custom presets
 * <now-date-range-picker
 *   preset="last 11 days"/>
 *   presets={[
 *     id: 'last 11 days',
 *     label: t('Last 11 days'),
 *     start: {
 *       offset: '-11',
 *       unit: 'days'
 *     }
 *   ]}
 * />
 * ```
 *
 * @seismicElement now-date-range-picker
 * @summary Component used to select a custom range of dates or select from a preset of date ranges.
 */
createCustomElement('now-date-range-picker', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Type determines the display view of the date-range-picker.
		 * Supported values are `standard`, `weekly`, `monthly`,`quarterly` and `yearly`.
		 * @type {string}
		 */
		type: {
			default: 'standard',
			schema: {
				type: 'string',
				enum: ['standard', 'weekly', 'monthly', 'quarterly', 'yearly']
			}
		},
		/**
		 * Date string for ending of the range that matches the passed in `format`
		 * @type {string}
		 */
		end: {},
		/**
		 * Toggles whether or not to show the Ok and Cancel buttons.
		 * @type {string}
		 */
		requireConfirmation: {
			default: false
		},
		/**
		 * Date format for the date string.
		 * This property is only relevant for `type='standard'` and is ignored for other views.
		 *
		 * When not provided, the default format is `YYYY-MM-DD` and `YYYY-MM-DD hh:mm:ss a` when
		 * `has-time` is set to true.
		 *
		 * @type {string}
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 *
		 */
		format: {},
		/**
		 * When true, enables selecting a time range.
		 *
		 * When start time is cleared, defaults to the beginning of that day.
		 * When end time is cleared, defaults to the end of that day.
		 *
		 * When not provided, the default format is `YYYY-MM-DD`.
		 * @type {boolean}
		 *
		 */
		hasTime: { default: false, type: 'boolean' },
		/**
		 * Label for the Start field that is shown to the user.
		 * @type {string}
		 */
		labelStart: { default: t('Start') },
		/**
		 * Label for the End field that is shown to the user.
		 * @type {string}
		 */
		labelEnd: { default: t('End') },
		/**
		 * String that defines the user's language and region.
		 * @type {string}
		 */
		language: { default: 'en-US' },
		/**
		 * Name for the component.
		 * @type {string}
		 *
		 */
		name: {},
		/**
		 * Optional callback that gets called with `{ start, end, preset }`.
		 * @type {function}
		 * @private
		 *
		 */
		onValueChange: {},
		/**
		 * The preset from `presets` to initially use.
		 * @type {string}
		 *
		 * When not provided, the default preset is custom
		 *
		 */
		preset: { default: 'custom' },
		/**
		 * Presets to display as selected range.
		 *
		 * Array of objects in the shape of
		 * { start: DateTransform, end: DateTransform }
		 *
		 * DateTransform: {
		 *   offset: String/Number providing the offset value for the `unit`
		 *   unit: String that is one of the units supported by moment.js
		 *   inclusive: Boolean determining if the side of range is inclusive
		 *
		 *   absolute: Boolean override that allows consumer to manually set the date
		 *   method: String for the method used when `absolute` is true to manipulate the date
		 *   value: String for the argument passed to `method` when `absolute` is true
		 * }
		 *
		 * @type {object}
		 * @see [Moment units](https://momentjs.com/docs/#/manipulating/add/
		 */
		presets: { default: defaultPresets },
		/**
		 * Date string for starting of the range that matches the passed in `format`
		 * @type {string}
		 */
		start: {},
		/**
		 * Toggles whether or not to show the TimeAgo Preset Panel for Standard View Only (type : `standard`)
		 * Not supported on `weekly`, `monthly`, `quarterly`, `yearly` type Views.
		 *
		 * Passing in false will hide the panel altogether.
		 * Passing in true will show the panel with default config which is {tableName: ''}.
		 * Passing in a configObject will show the panel with the config as passed in.
		 *
		 * tableName - tableName for the Business Calendar Relative fields.
		 *
		 * @type {(boolean|{ tableName: string })}
		 *
		 */
		timeAgo: {
			default: false,
			schema: {
				oneOf: [
					{ type: 'boolean' },
					{
						type: 'object',
						properties: {
							tableName: { type: 'string' }
						}
					}
				]
			}
		},
		/**
		 * Array of disabled dates defined as
		 * `[ (DATE | DATE_RANGE)* ]`
		 *
		 * `DATE`:
		 * 	Date string matching `format` property
		 *
		 * `DATE_RANGE`:
		 * 	{ start: `DATE`, end: `DATE` } | { after: `DATE` } | { before: `DATE` }
		 *
		 * where `start`/`end` are inclusive and `after`/`before` are exclusive
		 * eg. [ '13-01-2020', { start: '15-01-2020', end: '20-01-2020' }, { before: '05-01-2020' }, { after: '25-01-2020' }]
		 *
		 * @type { Array < string | {start: string, end: string} | {before: string} | {after: string} > }
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 */
		disabledDates: {
			default: [],
			schema: {
				type: 'array',
				items: [
					{ type: 'string' },
					{
						type: 'object',
						properties: { start: { type: 'string' }, end: { type: 'string' } },
						required: ['start', 'end']
					},
					{ type: 'object', properties: { before: { type: 'string' } } },
					{ type: 'object', properties: { after: { type: 'string' } } }
				]
			}
		},
		/**
		 * When true, enables Right to Left behavior (RTL)
		 * @type {boolean}
		 *
		 * When not provided, the default value is `false`
		 *
		 */
		reversed: { default: false, type: 'boolean' },

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean|string)}
		 */
		readonly: { default: false, type: 'boolean' }
	},
	styles,
	actionHandlers,
	behaviors: [{ behavior: rtlBehavior }],
	eventHandlers,
	dispatches: {
		/**
		 * Dispatched when the date range is changed.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_DATE_RANGE_PICKER#VALUE_CHANGED': {}
	}
});
