import moment from 'moment';
import { find, isNil, reduce } from 'lodash';

let applyTransformations = ({
	date,
	format,
	offset,
	unit,
	inclusive = false,
	absolute,
	method,
	value
}) => {
	let transformations = [
		// `moment.add` supports negative numbers + strings
		// if `undefined`, same as modifying nothing
		d => d.add(offset, unit),
		d => {
			if (absolute) {
				return d[method](value);
			}

			return inclusive ? d : d.add(1, 'day');
		}
	];

	return reduce(transformations, (acc, t) => t(acc), moment(date)).format(
		format
	);
};

export default function applyPreset({ presets, preset, date, format }) {
	let { start = {}, end = {} } = find(presets, { id: preset });

	// default inclusive to false for start, true for end
	return {
		start: applyTransformations({
			date,
			format,
			...start,
			inclusive: isNil(start.inclusive) ? false : start.inclusive
		}),
		end: applyTransformations({
			date,
			format,
			...end,
			inclusive: isNil(end.inclusive) ? true : end.inclusive
		})
	};
}
